import React from 'react';
import { Field, reduxForm } from 'redux-form';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';

import { paymentActions as pa} from '../actions/PaymentActions';
import { connect } from 'react-redux'
import { withStyles, makeStyles } from '@material-ui/core/styles';
import CircularProgress from '@material-ui/core/CircularProgress';

import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';

// Styles
const useStyles = makeStyles(theme => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: '100%',
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
}));

const required = value => value ? undefined : 'Required'
// Form fields
const renderField = ({ input, type, label, autoComplete, meta: { touched, error} }) => {

  return(
    <div>
     <TextField
      required
      {...input} 
      type={type}
      label={label}
      fullWidth
      autoComplete={autoComplete} 
      />
      {touched && (error && <span className="alert-danger">{error}</span>) }
    </div>    
)}
const apurpose = {"GP0001" : "Bonus",
                 "GP0002" : "Commission",
                 "GP0003" : "Expense",
                 "GP0004" : "Non-Taxable Payment",
                 "GP0005" : "Income",
                 "GP0006" : "Pension",
                 "GP0007" : "Charity Donation"
               };

/********************************************************************************/

const ColorCircularProgress = withStyles({
  root: {
    color: '#4058b5',
  },
})(CircularProgress);

const paymentMethod = (values, dispatch) => {
  return dispatch(pa.submitPayment(values));
}

const PaymentForm = props => {

  const {error, handleSubmit, pristine, reset, submitting, 
    submitSucceeded, onSubmit, isPayment, loading} = props;

  if(isPayment !== false && submitSucceeded !== false){
     onSubmit()
  }

 const classes = useStyles();
const renderPurposeSelect = ({ input, meta: { touched, error } }) => {
 
  let options = [];

  for (let [key, value] of Object.entries(apurpose)) {
    options.push(<MenuItem value={key} key={key}>{value}</MenuItem>)
  }

  return (<div>
    <FormControl className={classes.formControl}>
      <InputLabel id="purpose-type-select-label">Purpose</InputLabel>
      <Select
        labelId="purpose-type-select-label"
        id="purpose-select"
        {...input}         
        required
      >     
        {options}
      </Select>
    </FormControl>  

    {touched && error && <span className="alert-danger">{error}</span>}
  </div>)
};



  return (
    <React.Fragment>
      <Typography variant="h6" gutterBottom>
        Make Payment
      </Typography>
      <form onSubmit={handleSubmit(paymentMethod)} noValidate>
      <Grid container spacing={3}>
        <Grid item xs={12} md={6}>
          <Field
            name="currency"           
            type="text"
            component={renderField}
            label="Currency" 
            validate={required}
            autoComplete="currency"
          />
        </Grid>
        <Grid item xs={12} md={6}>
          <Field
            name="paymentid"           
            type="text"
            component={renderField}
            label="Client PaymentId" 
            validate={required}
            autoComplete="paymentid"
          />
        </Grid>
        <Grid item xs={12} md={6}>         
          <Field
            name="amount"           
            type="text"
            component={renderField}
            label="Amount" 
            validate={required}
            autoComplete="amount"
          />
        </Grid>  
        <Grid item xs={12} md={6}>        
          <Field name="purpose" validate={required} component={renderPurposeSelect} />
        </Grid>
        <Grid item xs={12} sm={6}>
          <Button 
            type="submit"
            variant="contained"
            color="primary">
            Finish Payout
          </Button>
        </Grid>
        <Grid item xs={12} sm={6}>
          {loading? <ColorCircularProgress size={30} thickness={5} />: ''} 
          { (submitSucceeded && !isPayment)?<span className="alert-danger">Payment failed!</span>: ''}
        </Grid>
      </Grid>
      </form>
    </React.Fragment>
  );
}


const mapStateToProps = (state) => {
  return {   
    loading: state.paymentFormReducer.loading,
    isPayment: state.paymentFormReducer.isPayment
  } 
}

const payForm =  reduxForm({
  form: 'paymentform', // a unique identifier for this form
  paymentMethod,
  initialValues:{currency: "USD" },

})(PaymentForm);

// You have to connect() to any reducers that you wish to connect to yourself
export default connect(mapStateToProps)(payForm);